<script>
export default {
  data () {
    return {
      swiperOptions: {
        slidesPerView: 2,
        loop: true,
        nextButton: '.swiper-button-next',
        prevButton: '.swiper-button-prev'
      }
    }
  },
  mounted () {
    let windowWidth = document.body.clientWidth
    if (windowWidth < 767) {
